@import url('https://fonts.googleapis.com/css2?family=Anton&family=Montserrat:wght@700&display=swap');

.to-dentro {
    border-radius: 50px;
    background: #cecece;
    white-space: nowrap;
    padding: 10px 22px;
    color: black;
    font-size: 20px;
    outline: none;
    border: none;
    /* border: 1px solid #cecece; */
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
}

.to-dentro:hover {
    transition: all 0.2s ease-in-out;
    background: #ababab;
    color: black;
}

.to-dentro:active {
    color: black;
}

@media screen and (max-width: 370px) {

    .to-dentro {
        font-size: 16px;
    }
}