.to-dentro-blue {
    border-radius: 50px;
    background: linear-gradient(to right, #ffd234, #ffb534);
    white-space: nowrap;
    padding: 10px 22px;
    color: #151515;
    font-size: 20px;
    outline: none;
    border: none;
    /* border: 1px solid #cecece; */
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
}

.to-dentro-blue:hover {
    transition: all 0.2s ease-in-out;
    background: linear-gradient(to right, #d3ae2b, #cf9837);
    color: #151515;
}

.to-dentro-blue:active {
    color: white;
}

@media screen and (max-width: 370px) {

    .to-dentro-blue {
        font-size: 16px;
    }
}