.to-dentro-nav {
    border-radius: 50px;
    background: #cecece;
    white-space: nowrap;
    padding: 10px 22px;
    color: black;
    font-size: 16px;
    outline: none;
    border: none;
    /* border: 1px solid #cecece; */
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
}

.to-dentro-nav:hover {
    transition: all 0.2s ease-in-out;
    background: #ababab;
    color: black;
}

.to-dentro-nav:active {
    color: black;
}

a {
    text-decoration: none;
}

.nav-div {
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin-top: 20px; */
}