.form-content {
    /* margin: 100px auto; */
    max-width: 600px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.2);
    position: relative;
    border-radius: 10px;

    display: flex;
    flex-direction: column;
 
    align-items: center;
    background: #151515;
    padding: 40px;
    color: white;
}

.form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 70px 20px 70px 20px;
    background: #3128af;
}

.waitlist {
    text-align: center;
    font-family: 'Anton', sans-serif;
    padding: 20px;
}

.form-inputs {
    display: flex;
    flex-direction: column;
    padding: 20px 0 20px 0;
}

.form-button {
    width: 100%;
    padding: 10px 0 10px 0;
    background: #3027ac;
    border: none;
    color: white;
    margin: 10px 0 10px 0;
    border-radius: 5px;
}

.form-input {
    padding: 10px;
    margin-top: 10px;
}

label {
    padding-bottom: 10px;
    font-size: 18px;
}

.error {
    font-size: 14px;
    margin: 5px;
    color: #ff3030;
}

.input-wrapp {
    display: flex;
    align-items: center;
    padding: 10px 0 10px 0;
}

.sent {
    text-align: center;
    padding-top: 20px;
}

.select-p {
    font-weight: 400;
    font-size: 15px;
}

@media screen and (max-width: 560px) {
    .form-content {
        padding: 30px;
    }
}

@media screen and (max-width: 300px) {
    .form-content {
        padding: 20px;
    }

    .form-container {
        padding: 15px ;
    }
}