.name {
    font-size: 32px;
    margin-bottom: 30px;
    color: #ffd234;
}

.topic {
    font-size: 18px;
    /* margin-bottom: 10px; */
}

.circle {
    display: flex;
    border-radius: 270px;
    background: linear-gradient(to right, #ffd234, #ffb534);
    justify-content: center;
    align-items: center;
    width: 40px;
    padding: 10px;
    height: 40px;
    margin-right: 10px;
}

.icon {
    color: #151515;
    
}

.info-wrapp {
    display: flex;
    background: white;
    padding: 15px 20px 15px 20px;
    border-radius: 8px;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
}