    max-width: 600px;
    border: #151515 solid 1px;
    border-radius: 5px;
    width: 100%;
}

.module {
    background: linear-gradient(to right, #ffd234, #ffb534);
    color: #151515;
    cursor: pointer;
    padding: 32px;
    display: flex;
    border: none;
    align-items: center;
    outline: none;
    transition: background-color 0.6 ease;
    /* font-weight: 300; */
    border-radius: 5px;
}

.module:hover
{
    background: linear-gradient(to right, #ffd234, #ffb534);
}

.activated {
    background: linear-gradient(to right, #ffd234, #ffb534);    
}

.module__title {
    font-weight: 400;
    font-size: 18px;
    text-align: left;
}

